<template>
  <div class="law-template g-in">
    <!-- top -->
    <div class="law-top clearfix">
      <el-breadcrumb separator-class="el-icon-arrow-right" class="fl">
        <el-breadcrumb-item :to="item.name?{name:item.name}:''" v-for="(item,index) in breadList" :key="index">{{item.title}}</el-breadcrumb-item>
      </el-breadcrumb>

      <div class="fr">
        <el-input placeholder="搜索法条..." v-model.trim="searchInfo" class="input-with-select" clearable @clear="clearSearch">
          <el-button slot="append" class="search-btn" @click="searchData">搜索</el-button>
        </el-input>
      </div>
    </div>

    <!-- list -->
    <div class="law-list clearfix">
      <!-- menu -->
      <div class="law-menu fl">
        <div class="all-menu">
          所有类目
        </div>
        <el-collapse>
          <el-collapse-item v-for="(item, index) in classes" :key="index">
            <template slot="title">
              <i :class="'iconfont '+item.icon"></i>&emsp;{{item.classes}}
            </template>
            <div :class="[item.children.length<7?'list-child':'list-child-li']" v-for="i in item.children" :key="i.classesId" @click="searchClasses(i)">{{i.classes}}</div>
          </el-collapse-item>
        </el-collapse>
      </div>
      <!-- law -->
      <div class="law-show-list fr">
        <div class="list-top clearfix">
          <span class="fl">{{lawTitle}}</span>
          <div class="fr">
            <el-radio-group v-model="timeliness" size="mini" @change="changeTimeliness">
              <template v-for="(item, index) in options" :keys="index">
                <el-radio-button :label="item.value" />
              </template>
            </el-radio-group>
          </div>
        </div>

        <ul class="list">
          <p class="noData" v-if="noDataText">抱歉，未找到您所需要的内容</p>
          <li class="clearfix" v-for="item in lawsList" :key="item.id">
            <p class="content fl" @click="goDetail(item)">{{ item.title }}</p>
            <p class="time fr">{{ (item.promulgaDate&&item.promulgaDate!=='null')?item.promulgaDate.split(' ')[0]:'' }}</p>
          </li>
        </ul>
        <el-pagination background layout="prev, pager, next" :total="total" :current-page="pageInfo.pageNumber" @current-change="handleCurrentChange"></el-pagination>

      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      searchInfo: '',
      breadList: this.$route.meta.bc,
      pageInfo: {
        pageNumber: 1,
        pageSize: 10
      },
      classes: [],
      iconList: ['icon-Fill', 'icon-falv', 'icon-gongchandang', 'icon-didian', 'icon-sifagongzheng', 'icon-hangzheng', 'icon-guizhangzhidu'],
      lawTitle: '',
      timeliness: '全部',
      options: [{
        value: '全部'
      }, {
        value: '有效'
      }, {
        value: '失效'
      }, {
        value: '已被修正'
      }, {
        value: '其他'
      }],
      total: 0,
      lawsList: [],
      noDataText: true,
      classesId: null,
      lastClasses: {}
    }
  },
  async created() {
    this.getSource()
    this.ajaxDataAll()
  },
  beforeDestroy() {
    this.breadList.splice(2, 1)
  },
  methods: {
    async ajaxDataAll() {
      const res = await this.$ajaxPost(this.projectName + '/law/finds.do', this.timeliness === '全部' ? {
        ...this.pageInfo
      }: {
        ...this.pageInfo,
        timeliness: this.timeliness
      })
      // console.log(res)
      this.noDataText = Array.isArray(res)
      this.lawsList = res.rows
      this.total = res.total
    },
    // 发送请求数据的ajax请求
    async ajaxData(classesId) {
      const res = await this.$ajaxPost(this.projectName + '/law/finds.do', {
        classesId,
        ...this.pageInfo
      })
      // console.log(res)
      this.noDataText = Array.isArray(res)
      this.lawsList = res.rows
      this.total = res.total
    },
    // 时效性查询数据ajax请求
    async ajaxDataTimeliness(classesId) {
      const res = await this.$ajaxPost(this.projectName + '/law/finds.do', {
        classesId,
        ...this.pageInfo,
        timeliness: this.timeliness
      })
      // console.log(res)
      this.noDataText = Array.isArray(res)
      this.lawsList = res.rows
      this.total = res.total
    },
    // 无类目 已经搜索 时效性查询数据ajax请求
    async ajaxDataNoMenuHasSearchTimeliness(classesId) {
      const res = await this.$ajaxPost(this.projectName + '/law/finds.do', {
        title: this.searchInfo,
        ...this.pageInfo,
        timeliness: this.timeliness
      })
      // console.log(res)
      this.noDataText = Array.isArray(res)
      this.lawsList = res.rows
      this.total = res.total
    },
    // 有类目 已经搜索 时效性查询数据ajax请求
    async ajaxDataHasMenuHasSearchTimeliness(classesId) {
      const res = await this.$ajaxPost(this.projectName + '/law/finds.do', {
        classesId,
        title: this.searchInfo,
        ...this.pageInfo,
        timeliness: this.timeliness
      })
      // console.log(res)
      this.noDataText = Array.isArray(res)
      this.lawsList = res.rows
      this.total = res.total
    },
    // 搜索查询
    async ajaxDataSearch(classesId) {
      let params = {}
      if(this.lawTitle === '') {
        params = {
          ...this.pageInfo,
          title: this.searchInfo
        }
      } else {
        params = {
          classesId,
          ...this.pageInfo,
          title: this.searchInfo
        }
      }
      const res = await this.$ajaxPost(this.projectName + '/law/finds.do', params)
      // console.log(res)
      this.noDataText = Array.isArray(res)
      this.lawsList = res.rows
      this.total = res.total
    },
    // 所有类目
    getSource() {
      this.$ajaxPost(this.projectName + '/law/findLawClessesList.do').then(res => {
        // console.log(res)
        let parentList = res.filter(item => item.parentId === 0)
        parentList.forEach((el, index) => {
          let arr = res.filter(i => i.parentId === el.classesId)
          if(arr.length !== 0){
            this.$set(el, 'children', arr)
          }else {
             let newarr = []
             newarr.push(el)
            this.$set(el, 'children', newarr)
          }
          this.$set(el, 'icon', this.iconList[index])
        })
        // console.log(parentList)
        this.classes = parentList
      })
    },
    // 所有类目点击切换，查询数据
    searchClasses(i) {
      this.searchInfo = ''
      this.timeliness = '全部'
      this.pageInfo.pageNumber = 1
      this.lawTitle = i.classes
      this.classesId = i.classesId
      this.breadList.slice(0, 1)
      if(this.breadList.length >= 3){
        this.breadList.splice(2, 1, {title: i.classes})
      } else {
        this.breadList.push({title: i.classes})
      }
      // console.log(i)
      this.ajaxData(this.classesId)
    },
    // 时效性切换(已选类目，未选类目)
    changeTimeliness(val) {
      this.pageInfo.pageNumber = 1
      if(this.lawTitle!=='') {
        // 点击类目 有classesId
        if(this.searchInfo===''){
          // 未搜索
          if(val === '全部'){
            this.ajaxDataSearch(this.classesId)
          } else {
            this.ajaxDataTimeliness(this.classesId)
          }
        }else {
          if(val === '全部') {
            this.ajaxDataSearch(this.classesId)
          } else {
            this.ajaxDataHasMenuHasSearchTimeliness(this.classesId)
          } 
        }
      }else {
        // 未点击类目 无classesId
        if(this.searchInfo===''){
          this.ajaxDataAll()
        }else {
          if(val === '全部') {
            this.ajaxDataSearch(this.classesId)
          }else{
            this.ajaxDataNoMenuHasSearchTimeliness(this.classesId)
          }
        }
      }
    },
    // 去法条详情
    goDetail(item) {
      // console.log(item)
      this.$router.push({
        name: 'lawdetail',
        params: {
          id: item.id
        }
      })
    },
    // 分页
    handleCurrentChange(val) {
      this.pageInfo.pageNumber = val
      this.ajaxData(this.classesId)
    },
    // 搜索
    async searchData() {
      this.pageInfo.pageNumber = 1
      this.timeliness = '全部'
      if(!this.searchInfo) {
        this.$message.warning('请输入搜索内容...')
        return
      }
      this.ajaxDataSearch(this.classesId)
    },
    // 清空搜索
    clearSearch() {
      this.timeliness = '全部'
      this.ajaxData(this.classesId)
    }
  }
}
</script>


<style lang="scss">
.law-template{
  margin: 40px auto !important;
  min-height: 600px;
  .law-top{
    height: 40px;
    .el-breadcrumb{
      display: inline-block;
      height: 100%;
      line-height: 40px;
    }
    .el-input{
      .el-input-group__append{
        background-color: #E0AD78;
        color: #fff;
        border: #E0AD78;
        border-left: none;
      }
    }
  }
  .law-list{
    margin-top: 30px;
    .law-menu{
      width: 326px;
      border: 1px solid #E4E4E4;
      user-select: none;
      .all-menu{
        height: 48px;
        line-height: 48px;
        box-sizing: border-box;
        padding: 0 20px;
        background-color: #fff;
      }
      .el-collapse-item__header{
        box-sizing: border-box;
        padding: 0 20px;
        background-color: #F5F5F5;
        color: #656565;
        i{
          color: #034766;
        }
      }
      .el-collapse-item__content{
        box-sizing: border-box;
        padding: 4px 20px;
        div.list-child{
          height: 30px;
          line-height: 30px;
          color: #3E3E3E;
          cursor: pointer;
          box-sizing: border-box;
          padding-left: 28px;
          &:hover, &:focus{
            background-color: #FBFBFB;
            border-radius: 4px;
            color: #034766;
          }
        }
        .list-child-li{
          display: inline-block;
          width: 28%;
          padding: 7px 0;
          margin-right: 5px;
          text-align: center;
          cursor: pointer;
          &:hover, &:focus{
            background-color: #FBFBFB;
            border-radius: 4px;
            color: #034766;
          }
        }
      }
    }
    .law-show-list{
      width: 846px;
      border: 1px solid #E4E4E4;
      background-color: #fff;
      box-sizing: border-box;
      padding: 20px 30px;
      .list-top{
        margin-bottom: 20px;
        >span{
          font-size: 15px;
          font-weight: 700;
          display: inline-block;
          height: 28px;
          line-height: 28px;
        }
      }
      .list{
        .noData{
          text-align: center;
          color: #ccc;
        }
        li{
          height: 56px;
          border-bottom: 1px solid #E8E8E8;
          cursor: pointer;
          &:last-child{
            border-bottom: 0;
          }
          &:hover{
            background-color: #FBFBFB;
            border-radius: 4px;
          }
          &:hover p.content, &:focus p.content{
            color: #00aec4;
          }
          &:hover .content::before, &:focus .content::before{
            background-color: #00aec4 !important;
          }
          .classes-tip{
            height: 100%;
            line-height: 56px;
            margin: 0;
            font-size: 12px;
            color: #fff;
            span{
              background-color: #00aec4;
              padding: 2px 4px;
              border-radius: 2px;
            }
          }
          .content{
            width: 80%;
            height: 100%;
            margin: 0;
            line-height: 56px;
            text-overflow: ellipsis;
            white-space: nowrap;
            overflow: hidden;
            color: #4A4A4A;
            font-size: 15px;
            box-sizing: border-box;
            padding-left: 20px;
            position: relative;
            &:before{
              content: '';
              display: block;
              width: 6px;
              height: 6px;
              background-color: #D8D8D8;
              position: absolute;
              top: 50%;
              left: 0;
              transform: translate(0, -50%);
            }
          }
          .time{
            font-size: 12px;
            color: #797979;
            height: 100%;
            margin: 0;
            line-height: 56px;
          }
        }
      }
      .el-pagination{
        box-sizing: border-box;
        padding: 10px 0;
        text-align: right;
      }
    }
  }
}
</style>



