<template>
  <div class="law-detail-template g-in">
    <!-- bread -->
    <div class="law-bread">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item :to="item.name?{ name: item.name }:''" v-for="(item, index) in breadList" :key="index">{{item.title}}</el-breadcrumb-item>
      </el-breadcrumb>
    </div>  
    <!-- content -->
    <div class="law-content">
      <!-- top -->
      <div class="law-content-top">
        <h2>{{ lawInfo.title }}</h2>
        <div class="top-tip">
          <div class="top-tip-content g-in">
            <div>
              <p>资料属性：<span>{{lawInfo.dataAttribute}}</span></p>
              <p>颁布文号：<span>{{lawInfo.documentNo}}</span></p>
            </div>
            <div>
              <p>部门分类：<span>{{lawInfo.department}}</span></p>
              <p>颁布日期：<span>{{(lawInfo.promulgaDate&&lawInfo.promulgaDate!=='null')?lawInfo.promulgaDate.split(' ')[0]:''}}</span></p>
            </div>
            <div>
              <p>制定机关：<span>{{lawInfo.formulateDepartment}}</span></p>
              <p>施行日期：<span>{{(lawInfo.implementaDate&&lawInfo.implementaDate!=='null')?lawInfo.implementaDate.split(' ')[0]:''}}</span></p>
            </div>
          </div>
          <span class="base">基本信息</span>
        </div>
      </div>
      <!-- detail -->
      <div class="detail-content" v-html="lawInfo.content"></div>
    </div>
    <BackTop></BackTop>
  </div>
</template>

<script>
export default {
  data() {
    return {
      breadList: this.$route.meta.bc,
      lawId: this.$route.params.id,
      lawInfo: {}
    }
  },
  created() {
    this.getLawContent()
  },
  methods: {
    async getLawContent() {
      const res = await this.$ajaxPost(this.projectName+'/law/find.do', {
        id: this.lawId
      })
      // console.log(res)
      this.lawInfo = res
      this.setBreadList()
    },
    setBreadList() {
      this.breadList = this.breadList.slice(0, 1)
      this.breadList.push({
        title: this.lawInfo.classes
      })
      this.breadList.push({
        title: '详情'
      })
    }
  }
}
</script>


<style lang="scss">
.law-detail-template{
  margin: 50px auto;
  min-height: 600px;
  .law-bread{
    background-color: #FFF;
    box-sizing: border-box;
    padding: 0 20px;
    height: 48px;
    margin: 50px 0 20px;
    .el-breadcrumb{
      line-height: 48px;
    }
  }
  .law-content{
    background-color: #fff;
    box-sizing: border-box;
    padding: 35px 20px;
    
    .law-content-top{
      width: 100%;
      border-bottom: 1px solid #E8E8E8;
      margin-bottom: 30px;
      h2{
        width: 70%;
        margin: 0 auto;
        text-align: center;
        line-height: 1.5;
      }
      .top-tip{
        width: 100%;
        height: 124px;
        box-shadow: 0px 0px 4px 0px rgba(0, 0, 0, 0.1),0px 2px 4px 0px rgba(0, 0, 0, 0.15);
        margin: 20px 0;
        position: relative;
        .top-tip-content{
          width: 80%;
          height: 100%;
          line-height: 124px;
          >div{
            width: 32.8%;
            display: inline-block;
            vertical-align: middle;
            p{
              margin: 10px 0;
              color: #3E3E3E;
            }
          }
        }
        .base{
          position: absolute;
          top: 20px;
          left: -2px;
          padding: 8px 10px;
          color: #fff;
          background-color: #034766;
        }
      }
    }
    .detail-content{
      box-sizing: border-box;
      padding: 0 20px;
      line-height: 1.5;
    }
  }
}
</style>



